/* Split the screen in half */
.split {
	height: 100%;
	width: 50%;
	position: fixed;
	z-index: 1;
	top: 0;
	overflow-x: hidden;
	padding-top: 20px;
}

/* Control the left side */
.left {
	left: 0;
}

/* Control the right side */
.right {
	right: 0;
}

.textArea {
	margin-left: 1.5%;
}

#txtArea {
	border: 1px black solid;
	width: 100%;
	height: 95%;
	padding-left: 10px;
	font-size: 18pt;
	resize: none;
	outline: none;
	font-family: "Segoe UI", Tahoma, Geneva, Verdana, sans-serif;
}

#result {
	font-size: 40pt;
	text-align: center;
	font-family: "Segoe UI", Tahoma, Geneva, Verdana, sans-serif;
	color: rgb(46, 102, 206);
}
